*,
*::before,
*::after {
	box-sizing: border-box;
}

body {
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
	margin: 0;
	display: flex;
	justify-content: center;
	background-color: rgb(83, 92, 105);
	position: relative;

	&::after {
		content: '';
		background: url('./assets/keys.jpg') no-repeat center/cover fixed;
		opacity: 0.3;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		position: absolute;
		z-index: -1;
	}
}

.wrapper {
	margin: 10rem 0;
	padding: 1rem;
	border-radius: 0.5rem;
	box-shadow: 15px 15px 25px rgba(0, 0, 0, 0.6), -2px -2px 10px rgba(212, 226, 255, 0.068),
		inset 1px 1px 1px #373c3f;

	.calculator-grid {
		width: 100%;
		display: grid;
		grid-template-columns: repeat(4, 6rem);
		grid-template-rows: minmax(7rem, auto) repeat(5, 6rem);

		.output {
			grid-column: 1 / -1;
			display: flex;
			flex-direction: column;
			align-items: flex-end;
			justify-content: space-around;
			padding: 0.75rem;
			word-wrap: break-word;
			word-break: break-all;
			min-height: 80px;
			border-bottom: 1px solid #ffffff28;

			.prev-operand {
				font-size: 1.5rem;
			}

			.curr-operand {
				font-size: 2.5rem;
			}
		}

		.span-two {
			grid-column: span 2;
		}

		button {
			border: none;
			cursor: pointer;
			font-size: 1.5rem;
			background-color: transparent;
			padding: 0.75rem;
			margin: 0.3rem;
			border-radius: 1.9rem;
			transition: all 100ms;

			&:hover,
			&:focus {
				box-shadow: inset 2px 4px 16px rgba(21, 21, 21, 0.5);
			}

			&:active {
				transform: scale(0.96);
			}
		}
	}
}
